"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardFooter } from "@/components/ui/card";

interface CartItemProps {
  startingAmount: number;
  initialQuantity?: number;
}

export default function CartCard({ startingAmount, initialQuantity = 1 }: CartItemProps) {
  const [quantity, setQuantity] = useState(initialQuantity);

  const handleIncrease = () => setQuantity(quantity + 1);
  const handleDecrease = () => setQuantity(quantity > 1 ? quantity - 1 : 1);

  const subtotal = startingAmount * quantity;
  const taxes = 59;
  const total = subtotal + taxes;

  return (
    <Card className="bg-gray-100 w-full max-w-md rounded-2xl shadow-md p-4 space-y-4">
      <CardContent className="space-y-2">
        {/* Starts at */}
        <div className="flex justify-between">
          <span className="font-medium">Starts at</span>
          <span>₹{startingAmount}</span>
        </div>

        {/* Quantity */}
        <div className="flex justify-between items-center">
          <span className="font-medium">Quantity</span>
          <div className="flex items-center gap-2">
            <Button
              className="px-2 py-1 bg-gray-300 text-black rounded-md"
              onClick={handleDecrease}
            >
              -
            </Button>
            <span>{quantity}</span>
            <Button
              className="px-2 py-1 bg-gray-300 text-black rounded-md"
              onClick={handleIncrease}
            >
              +
            </Button>
          </div>
        </div>

        {/* Subtotal */}
        <div className="flex justify-between">
          <span className="font-medium">Subtotal</span>
          <span>₹{subtotal}</span>
        </div>

        {/* Taxes */}
        <div className="flex justify-between">
          <span className="font-medium">Taxes</span>
          <span>₹{taxes}</span>
        </div>

        {/* Divider */}
        <div className="border-t border-gray-400 my-2"></div>

        {/* Total */}
        <div className="flex justify-between text-lg font-bold">
          <span>Total</span>
          <span>₹{total}</span>
        </div>
      </CardContent>

      <CardFooter className="pt-2">
        <Button className="w-full">
          Confirm
        </Button>
      </CardFooter>
    </Card>
  );
}
